.hero {
  max-width: $max-width;
  height: 700px;
  padding: 112px 0 184px;
  margin: 0 auto;
  background-image: url("../../assets/img/me.png");
  background-repeat: no-repeat;
  background-position: right bottom;
  //z-index: -1;
  background-size: 40vw;
  position: relative;
  z-index: 1;
  &__title {
    max-width: 780px;
    font-style: normal;
    font-weight: 700;
    font-size: 2.5rem;
    line-height: 3.5rem;
  }

  &__link {
    color: $lightblue;
  }

  &__mail {
    color: $lightblue;
    font-size: 1.25rem;
    text-decoration: none;
    padding-bottom: 8px;
    border-bottom: 2px solid $lightblue;
    z-index: 1;
  }

  &__description {
    font-size: 1.25rem;
    max-width: 780px;

    font-style: normal;
    font-weight: 400;
    line-height: 3.5rem;
    margin-bottom: 3.75rem;
  }
}